import { SymptomSeverity, SymptomDuration } from '../types/symptom';
import { Gender } from '../lib/genderUtils';

export type Language = 'en' | 'de' | 'ro';



interface LanguageContextType {
  language: Language;
  setLanguage: (language: Language) => void;
  t: (key: string) => string;
  translateStatus: (status: CaseStatus) => string;
  translateSymptomSeverity: (severity: SymptomSeverity) => string;
  translateSymptomDuration: (duration: SymptomDuration) => string;
  translateGender: (gender: Gender) => string;
}

const LanguageContext = createContext<LanguageContextType | undefined>(undefined);

// Country to language mapping
const countryToLanguage: Record<string, Language> = {
  // German-speaking countries
  'DE': 'de', // Germany
  'AT': 'de', // Austria
  'CH': 'de', // Switzerland
  'LI': 'de', // Liechtenstein
  'LU': 'de', // Luxembourg
  
  // Romanian-speaking countries
  'RO': 'ro', // Romania
  'MD': 'ro', // Moldova
  
  // Default to English for all other countries
  'DEFAULT': 'en'
};

// Function to detect user's country and set appropriate language
const detectUserLanguage = async (): Promise<Language> => {
  // Try multiple IP geolocation services for better reliability
  const services = [
    'https://ipapi.co/json/',
    'https://ipinfo.io/json',
    'https://api.ipify.org?format=json'
  ];

  for (const service of services) {
    try {
      const response = await fetch(service, {
        method: 'GET',
        headers: {
          'Accept': 'application/json',
        },
      });
      
      if (!response.ok) continue;
      
      const data = await response.json();
      
      // Different services return country code in different fields
      const countryCode = data.country_code || data.country || data.countryCode;
      
      if (countryCode) {
        const detectedLanguage = countryToLanguage[countryCode] || countryToLanguage['DEFAULT'];
        return detectedLanguage;
      }
    } catch {
      continue;
    }
  }
  
  return detectBrowserLanguage();
};

// Fallback function to detect language from browser settings
const detectBrowserLanguage = (): Language => {
  try {
    // Get browser language
    const browserLang = navigator.language || navigator.languages?.[0] || 'en';
    
    // Extract language code (e.g., 'de-DE' -> 'de')
    const langCode = browserLang.split('-')[0].toLowerCase();
    
    // Map browser language to our supported languages
    if (langCode === 'de') return 'de';
    if (langCode === 'ro') return 'ro';
    
    // Default to English for all other languages
    return 'en';
  } catch {
    return 'en';
  }
};

